// Added 
.addedIntegrationCardContainer {
  container: card-container / inline-size;
  width: 100%;

  .card {
    border: 1px solid var(--vscode-panel-border);
    background: var(--vscode-editor-background);
    border-radius: 3px;
    color: var(--vscode-descriptionForeground);
  }

  .cardHeaderButton {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    background: transparent;
    appearance: none;
    font-family: inherit;
    text-align: left;
    border: none;
    padding: 8px;
    cursor: pointer;

    &:hover {
      .cardIcon {
        visibility: hidden;
      }
      .chevronIcon {
        color: var(--vscode-foreground);
      }
      .optionsButton {
        opacity: 1;
      }
    }
  }

  .leftSideOfHeader {
    display: flex; 
    align-items: center;
    gap: 8px;

    @container card-container (inline-size < 150px) {
      flex-direction: column;
      align-items: flex-start;
    }
  }

  .headerIconArea {
    position: relative;
    display: flex;
  }

  .cardIcon {
    width: 16px;
    height: 16px;
  }

  .optionsButton {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    opacity: 0;

    &:focus-visible,
    &:focus-within {
      opacity: 1;

      & ~ .cardIcon {
        visibility: hidden;
      }
    }
  }

  .cardName {
    color: var(--vscode-descriptionForeground);
    font-size: 9px;
    font-weight: 700;;
    margin-block-end: 2px;
    display: block;
  }

  .cardHeading {
    font-size: 14px;
    color: var(--vscode-foreground);
    font-weight: 400;
  }

  .itemText {
    color: var(--vscode-foreground);
  }

  .chevronIcon {
    transform: scaleY(1);
    transition: transform .1s ease;
    color: var(--vscode-descriptionForeground);
  }

  .cardIsOpen {
    .chevronIcon {
      transform: scaleY(-1);
    }
  }

  .cardContent {
    display: flex;
    flex-direction: column;
    gap: 12px;
    padding: 0 8px 8px 8px;
  }

  .linkContainer {
    display: flex;
    align-items: center;

    a {
      text-decoration: none;
    }
  }

  .itemIcon {
    width: 16px;
    height: 16px;
    margin-inline-end: 8px;
    color: var(--vscode-descriptionForeground);
  }

  .linkCardDivider {
    border-color: var(--vscode-panel-border);
    margin-block: 0;
  }

  .cardDivider {
    border-color: var(--vscode-panel-border);
    margin: 0;
  }

  .widgetLoading {
    pointer-events: none;
    text-decoration: none;
    opacity: 0.7
  }

  .subtitle {
    font-size: x-small;
    line-height: 1;
    display: block;
  }
}


// Unadded 
.unaddedIntegrationCardContainer {
  container: card-container / inline-size;
  background: var(--vscode-editor-background);

  .integrationCard {
    display: grid;
    grid-template-areas: "icon body label";
    grid-template-columns: auto 1fr auto;
    width: 100%;
    border: 1px solid var(--vscode-panel-border);
    background: transparent;
    border-radius: 3px;
    text-align: left;
    gap: 8px;
    padding: 8px;
    appearance: none;
    font-family: inherit;
    color: var(--vscode-descriptionForeground);
    cursor: pointer;
  }

  .cardIcon {
    grid-area: icon;
    margin-inline-end: auto;
    width: 16px;
    height: 16px;
  }

  .cardHeading {
    color: var(--vscode-foreground);
    font-weight: 500;
  }

  .cardBody {
    grid-area: body;
  }

  .cardText {
    margin-top: 2px;
    line-height: 14px; // optical tweak
  }

  .cardTag {
    margin-left: auto;
    grid-area: label;
    line-height: normal;
    width: min-content;
  }

  @container card-container (inline-size < 250px) {
    .integrationCard {
      grid-template-areas: "icon label"
                           "body body";
      grid-template-columns: auto 1fr;
    }
  }
}