.copyParent {
  opacity: 1;
  overflow: hidden;
}

.copyParent:hover .copyButton {
  opacity: 1;
}

.copyButton {
  opacity: 0;
}

.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.Table {
  display: flex;
  flex-direction: column;
  border: 1px solid var(--vscode-tab-border);
  margin-top: 0.5rem;
  line-height: 1.5;
  justify-content: space-between;
}

.Table-header {
  border-bottom: 1px solid var(--vscode-tab-border);
}

.Table-row {
  display: -webkit-box;
  display: -moz-box;
  display: box;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-flex-flow: row nowrap;
  -moz-flex-flow: row nowrap;
  flex-flow: row nowrap;
}

/*.Table-row:nth-of-type(even) {
    background-color: #ffffff;
  }
  .Table-row:nth-of-type(odd) {
    background-color: #f2f2f2;
  }*/

.Table-row-item {
  display: flex;
  padding: 0.5em;
  /* border: 1px solid #fff; */
  word-break: break-word;
}

.column-port {
  min-width: 50px;
}

.column-pid {
  min-width: 50px;
}

.selectable {
  user-select: all;
  cursor: auto;
}

.headingWithBadge {
  display: flex;
  align-items: center;
  gap: 8px;

  vscode-badge {
    margin: -2px 0; // lay out same size as heading
  }
}

.emptyMessage.emptyMessage {
  // up the specificity
  font-style: italic;
  margin: 16px 0;
}
