.collapsibleSection {
  display: flex;
  flex-direction: column;
}

.header {
  margin-left: -4px; // optical alignment for chevron
  appearance: none;
  display: flex;
  background: transparent;
  align-items: center;
  color: var(--vscode-descriptionForeground);
  gap: 4px;
  border: 0;
  padding: 0;
  font-family: inherit;
  cursor: pointer;

  &:hover {
    color: var(--vscode-foreground);
  }

  .title {
    text-align: start;
    flex: 1 1 0;
  }
  
  .actions {
    display: flex;
    align-items: center;
    gap: 4px;
    height: 16px; // lay out as 16px to avoid having the entire header jump
  }  
}

.chevronIcon {
  transform: rotate(-90deg);
  transition: transform .1s ease;
}

.collapsibleSection.isOpen {
  .chevronIcon {
    transform: rotate(0deg);
  }
}

.content {
  margin-top: 8px;
}