.banner {
  border: 1px solid var(--vscode-inputOption-activeBorder);
  border-radius: 3px;
  padding: 12px;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  gap: 12px;
  position: relative;

  &:before {
    content: '';
    z-index: -1;
    position: absolute;
    border-radius: inherit;
    inset: 0;
    background: var(--vscode-inputOption-activeBackground);
    opacity: .2;
    pointer-events: none;
  }
}

.closeIcon {
  position: absolute;
  right: 8px;
  top: 8px;
}

.markdownUl,
.markdownOl {
  padding-left: 20px;
}

.buttonLeftIcon {
  margin-right: var(--space-small);
}
