.rail {
  border: 1px solid var(--vscode-panel-border);
  background: var(--vscode-editor-background);
  border-radius: 3px;
  padding: 4px 8px;
  color: var(--description-foreground);
  display: flex;
  align-items: stretch;
  gap: 4px;
}

.item {
  flex: 1 1 0;
  appearance: none;
  background-color: transparent;
  cursor: pointer;
  display: flex;
  font-family: inherit;
  flex-direction: column;
  align-items: center;
  gap: 4px;
  padding: 6px 4px 4px;
  border: 0;
  color: var(--description-foreground);
  border-radius: 1.5px;

  &:hover {
    background-color: var(--vscode-toolbar-hoverBackground);
  }
  &:active {
    background-color: var(--vscode-toolbar-activeBackground);
  }
}

.troubleshootingInfo {
  padding: 4px var(--menu-side-padding); // CSS var provided by menu container
  display: flex;
  flex-direction: column;
  gap: var(--space-medium);

  dl {
    display: flex;
    flex-direction: column;
    margin: 0;
  }

  dd {
    margin: 0;
    font-family: var(--vscode-editor-font-family);
  }
}
