.tile {
  display: flex;
  align-items: center;
  gap: 8px;
}

.icon {
  width: 24px;
  height: 24px;
  object-fit: contain;
}

.info {
  display: flex;
  flex-direction: column;
  flex: 1 1 0;
}

.workspaceId {
  font-family: var(--editor-font-family, 'JetBrains Mono', monospace);
}

.actions {
  display: flex;
  align-items: center;
  gap: 4px;
}